/*--------------------------------------
* ヘッダーロゴ+ナビを囲む
--------------------------------------*/
.gm_area .header_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.gm_area .header_row .header_col1 {
  width: 4em;
}
.gm_area .header_row .header_col2 {
  width: calc(100% - 4em);
}
.gm_area .header_logo {
  text-align: center;
  display: block;
  font-weight: bold;
}